import React from "react";
import PropTypes from "prop-types";
import en from "../i18n/en";
import es from "../i18n/es";

const languages = {
  en,
  es
};

//not a bad first whack at it.
//https://stackoverflow.com/questions/33413880/react-redux-and-multilingual-internationalization-apps-architecture
export default function T() {
  return Component => {
    class TranslationComponent extends React.Component {
      render() {
        const currentLanguageStrings = languages[this.context.currentLanguage];
        // console.log(
        //   "current language: ",
        //   this.context.currentLanguage,
        //   currentLanguageStrings
        // );
        return (
          <Component
            {...this.props}
            {...this.state}
            strings={currentLanguageStrings || {}}
          />
        );
      }
    }

    TranslationComponent.contextTypes = {
      currentLanguage: PropTypes.string
    };

    return TranslationComponent;
  };
}

export function translate(lang, key) {
  const currentLanguageStrings = languages[lang];
  console.log(currentLanguageStrings, key);
  return currentLanguageStrings[key];
}



