import React from "react";
import T from "../i18n/translate.js";
import "./Scorer.css";
import PropTypes from "prop-types";

function Scorer({
  player,
  totalPoints,
  possiblePoints,
  addPoints,
  addRoundPoints,
  stopAddingPoints,
  strings
}) {
  return (
    <div>
      <div className="pointsFor">
        <div>
          {strings.addingPointsFor}:
          {" "}
          <strong>{player.name}</strong>
        </div>
        <div>{strings.pointsForRound}: <strong>{totalPoints}</strong></div>
      </div>

      <div className="pointsGrid">
        {possiblePoints.map(points => (
          <button
            key={points}
            className="scorerButton"
            onClick={() => addPoints(points)}
          >
            {points === 4000 ? "🐸 4000 🐸" : points}
          </button>
        ))}
      </div>

      <div>
        <button
          type="button"
          className="completeRoundBtn"
          onClick={() => addRoundPoints(totalPoints)}
        >
          {strings.completeRound}
        </button>
      </div>
      <div className="cancel">
        <button type="button" className="cancelBtn" onClick={stopAddingPoints}>
          {strings.cancel}
        </button>
      </div>
    </div>
  );
}

Scorer.propTypes = {
  player: PropTypes.shape({
    name: PropTypes.string,
    score: PropTypes.number,
    roundsPlayed: PropTypes.number
  }).isRequired,
  totalPoints: PropTypes.number.isRequired,
  possiblePoints: PropTypes.arrayOf(PropTypes.number).isRequired,
  addPoints: PropTypes.func.isRequired,
  addRoundPoints: PropTypes.func.isRequired,
  stopAddingPoints: PropTypes.func.isRequired,
  strings: PropTypes.object.isRequired
};

export default T()(Scorer);



