import React, { Component } from "react";
import PropTypes from "prop-types";
import PlayerList from "./PlayerList";
import SapoStorage from "../data/SapoStorage.js";
import { translate } from "./i18n/translate.js";

class SapoScoresContainer extends Component {
  constructor() {
    super();

    var T = key => {
      return translate(this.context.currentLanguage, key);
    };

    this.addPlayer = () => {
      const playerData = this.state.data;
      const newPlayer = {
        name: T("player") + " " + (playerData.length + 1),
        score: 0,
        roundsPlayed: 0
      };

      let newPlayerName = window.prompt(
        T("addPlayerMessage") + ":",
        newPlayer.name
      );

      newPlayer.name = newPlayerName || newPlayer.name;
      playerData.push(newPlayer);

      this.setState({ data: playerData });
    };

    this.startOver = () => {
      if (!window.confirm(T("newGameMessage"))) return;

      const currentState = this.state;
      currentState.data = currentState.data.map(player => {
        return { name: player.name, score: 0 };
      });
      currentState.addingPointsPlayerIndex = null;

      this.setState(currentState);
    };

    this.addPoints = points => {
      console.log("Adding Points:", points);
      const playerData = this.state.data;
      playerData[this.state.addingPointsPlayerIndex].score += points;

      this.setState({ data: playerData });
    };

    this.addRoundPoints = roundPoints => {
      console.log("Adding Round Points:", roundPoints);
      const playerData = this.state.data;
      const player = playerData[this.state.addingPointsPlayerIndex];
      player.score += roundPoints;
      player.roundsPlayed = ~~player.roundsPlayed + 1;

      this.setState({ data: playerData, addingPointsPlayerIndex: null });
    };

    this.stopAddingPoints = () => {
      this.setState({ addingPointsPlayerIndex: null });
    };

    this.doneAdding = () => {
      this.setState({ addingPointsPlayerIndex: null });
    };

    let initialPlayerData = [];

    SapoStorage.getScores()
      .then(value => {
        console.debug("Loaded previous scoreboard:", value);

        initialPlayerData = value || [];

        this.setState({ data: initialPlayerData });
      })
      .catch(ex => {
        console.error("err:", ex);
      });

    this.beginAddingPoints = (player, i) => {
      console.log("Adding points for:", player.name, i);
      this.setState({ addingPointsPlayerIndex: i });
    };

    this.deletePlayer = (player, i) => {
      if (!window.confirm(T("removePlayerMessage"))) return;

      const playerList = this.state.data;
      playerList.splice(i, 1);
      this.setState({ data: playerList });
    };

    this.editPlayer = (player, i) => {
      let newPlayerName = prompt(T("editPlayerMessage") + ":", player.name);
      if (newPlayerName) {
        var currentState = this.state.data;
        currentState[i].name = newPlayerName;
        this.setState({ data: currentState });
      }
    };

    this.state = {
      addingPointsPlayerIndex: null
    };
  }
  componentDidUpdate(prevProps, prevState) {
    console.debug(
      "Attempting to save data.",
      prevProps,
      prevState,
      prevState.data
    );

    if (prevState.data === undefined) {
      return;
    }

    SapoStorage.persistScores(this.state.data)
      .then(() => {
        console.info("Saved scores.");
      })
      .catch(ex => {
        console.error("Something went wrong while saving data", ex);
      });
  }
  render() {
    return (
      <div>
        <PlayerList
          addingPointsPlayerIndex={this.state.addingPointsPlayerIndex}
          data={this.state.data}
          addPoints={this.addPoints}
          addRoundPoints={this.addRoundPoints}
          stopAddingPoints={this.stopAddingPoints}
          doneAdding={this.doneAdding}
          startOver={this.startOver}
          addPlayer={this.addPlayer}
          deletePlayer={this.deletePlayer}
          editPlayer={this.editPlayer}
          beginAddingPoints={this.beginAddingPoints}
        />
      </div>
    );
  }
}

SapoScoresContainer.contextTypes = {
  currentLanguage: PropTypes.string
};

export default SapoScoresContainer;



