import localforage from "localforage";
const SapoStorage = {};

localforage.config({ name: "sapo_scores" });

const SCOREBOARD = "scoreboard";
const LANGUAGE = "language";
const DEFAULT_LANGUAGE = "en";

SapoStorage.getScores = () => {
  return localforage.getItem(SCOREBOARD);
};

SapoStorage.persistScores = scores => {
  return localforage.setItem(SCOREBOARD, scores);
};

SapoStorage.getLanguage = () => {
  return localforage.getItem(LANGUAGE).then(lang => {
    return lang || DEFAULT_LANGUAGE;
  });
};

SapoStorage.setLanguage = lang => {
  return localforage.setItem(LANGUAGE, lang);
};

export default SapoStorage;



