import React, { Component } from "react";
import Scorer from ".";

class ScorerContainer extends Component {
  constructor() {
    super();

    const possiblePoints = [
      300,
      400,
      500,
      600,
      700,
      800,
      900,
      1000,
      2000,
      4000
    ];

    this.possiblePoints = possiblePoints;

    this.addPoints = points => {
      console.log("Adding round points:", points);
      var currentState = this.state;
      currentState.totalPoints += points;
      this.setState(currentState);
    };

    this.state = { totalPoints: 0 };
  }
  render() {
    return (
      <Scorer
        player={this.props.player}
        totalPoints={this.state.totalPoints}
        addPoints={this.addPoints}
        addRoundPoints={this.props.addRoundPoints}
        possiblePoints={this.possiblePoints}
        stopAddingPoints={this.props.stopAddingPoints}
      />
    );
  }
}

export default ScorerContainer;



